import CommApi from "@/pages/comm/api.js"
import moment from '@/pages/comm/moment-with-locales.min.js'

export default {
	data() {
		return {
			openId: null,
			activeList: [],
			moment
		}
	},
	
	created() {
		this.userinfo = uni.getStorageSync("userinfo")
		this.openId = this.userinfo && this.userinfo.openid;
		this.getActiveTeams();
	},
	
	methods: {
		goDetail(obj, index) {
			const that = this;
			uni.showModal({
			    title: '预约取消',
			    content: '是否取消' + obj.hdzt + '的预约',
			    success: function (res) {
			        if (res.confirm) {
						uni.request({
							url: CommApi.api.cancelSubTeam.real,
							data: JSON.stringify({
								"openid": that.openId || 0,
								id: obj.id,
							}),
							method: 'POST',
							header: {
								"Access-Control-Allow-Origin": '*',
								"content-type": "application/json",
							},
							success: (res) => {
								if (res.statusCode === 200 && res.data.code === 1) {
									that.getActiveTeams();
								} else {
									uni.showToast({
										title: res.data && res.data.msg || res.data.error,
										icon: "none"
									});
								}
							}
						});
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		getActiveTeams() {
			const that = this;
			uni.request({
				url: CommApi.api.getActiveSubTeam.real,
				data: JSON.stringify({
					"openid": that.openId || 0,
				}),
				method: 'POST',
				header: {
					'content-type': 'application/json',
					"content-type": "application/json",
				},
				success: (res) => {
					if (res.statusCode === 200 && res.data.code === 1) {
						// this.swiperImages = res.data.data;
						this.activeList = res.data.data
						console.log(res.data.data)
					} else {
						uni.showToast({
							title: res.data && res.data.msg || res.data.error,
							icon: "none"
						});
					}
				}
			});
		},
	},
	//props: ['openId'],
}
